import React, { useState, useEffect } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, message } from 'antd';
import { TableData, ColEditDelete, CartHeadAction } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { getSubjectTypes, deleteSubjectType } from 'services/subjectTypeService';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DAO_TAO__LOAI_MON_HOC__NHAP_DU_LIEU;

const SubjectType = () => {
  const [reload, setReload] = useState(0);

  useEffect(() => {}, []);

  const handleDelete = async id => {
    try {
      await deleteSubjectType(id);
      message.success('Xa thnh cng');
      setReload(reload + 1);
    } catch (err) {
      console.log(err);
    }
  };

  const getSubjectTypesWithIndex = async () => {
    const res = await getSubjectTypes();
    let index = 0;
    res.data.items.forEach(item => {
      index += 1;
      item.index = index;
    });
    return res;
  };

  const columns = [
    {
      title: 'STT',
      dataIndex: 'index',
      key: 'index',
      width: '100px',
    },
    {
      title: 'Loi mn hc',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          linkEdit={{
            to: {
              pathname: ROUTER_FORM_DATA,
              search: `?id=${record.id}`,
            },
          }}
          onConfirmDelete={() => handleDelete(record.id)}
        />
      ),
    },
  ];

  return (
    <Card title="Loi mn hc">
      <CartHeadAction>
        <Link to={ROUTER_FORM_DATA}>
          <BtnAddNew />
        </Link>
      </CartHeadAction>
      <Row>
        <Col span={24}>
          <TableData
            reload={reload}
            columns={columns}
            service={getSubjectTypesWithIndex}
            rowKey="id"
          />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <SubjectType />
  </Page>
);



